.bdt-price-table-price {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	flex-direction: row;
	color: #555;
	font-weight: 800;
	font-size: 65px;
	padding: 30px 0;
	transition: all .3s ease;
}

.bdt-price-table-original-price {
	margin-right: 15px;
	text-decoration: line-through;
	color: #ce362b;
	font-family: "Oswald",Sans-serif;
	align-self: flex-start;
}

.elementor-typo-excluded {
	line-height: initial;
	letter-spacing: initial;
	text-transform: initial;
	font-weight: initial;
	font-size: initial;
	font-style: initial;
}

.bdt-price-table-currency {
	line-height: 1;
	margin-right: 3px;
	font-size: calc(20em/100);
	align-self: flex-start;
}

.bdt-price-table-integer-part {
	line-height: 0.8;
}

.bdt-price-table-after-price {
	display: flex;
	flex-wrap: wrap;
	text-align: left;
	align-self: stretch;
	align-items: flex-start;
	flex-direction: column;
	justify-content: flex-start;
}

.bdt-price-table-fractional-part {
	line-height: 1;
	font-size: calc(20em/100);
}

.bdt-price-table-period {    
	transition: color .3s ease;
	font-family: "Open Sans",Sans-serif;
}

.bdt-price-table-period-position-beside {
	margin-left: 10px;
}

.bdt-test {
	font-size:12px;
	color:black;
	font-weight:bold;
	font-style: italic
}

.bdt-asterisk {
	color: #ce362b;
	font-size:13px;
}
